import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getReportDisciplines, deleteDiscipline } from 'services/reportDiscipline';
import { dateToString } from 'utils/formatUtil';
import FormSearch from './FormSearch';
import './style.scss';

class ListReportDisciplinesPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteDiscipline(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Số quyết định',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày quyết định',
        dataIndex: 'approvalDate',
        render: (text, record) => dateToString(record.approvalDate, 'DD/MM/YYYY'),
      },
      {
        title: 'Nội dung',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/reportDisciplines/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Báo cáo quá trình kỷ luật">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/reportDisciplines/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getReportDisciplines}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListReportDisciplinesPage />
  </Page>
);



